@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans Light"), local("OpenSans-Light"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"), local("OpenSans-Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}

html {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
}

body {
  min-width: 320px;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.rectangle-button {
  box-sizing: border-box;
  padding: 16px 20px 17px;
  font: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  border: none;
  border-radius: 3px;
  background-color: #2aab6d;
}

.rectangle-button:hover:not(:disabled),
.rectangle-button:focus {
  background-color: #1c9b5e;
}

.rectangle-button:active:not(:disabled) {
  color: rgba(255, 255, 255, 0.3);
}

.rectangle-button:disabled {
  background-color: #e5e5e5;
}

.rounded-button {
  padding: 15px 10px 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  border-width: 3px;
  border-style: solid;
  background-color: transparent;
}

.rounded-button--theme-white {
  border-color: #ffffff;
  border-radius: 40px;
}

.rounded-button--theme-white:hover {
  color: #d22856;
  background-color: #ffffff;
  box-shadow: 0 5px 7px 0 rgba(1, 1, 1, 0.75);
}

.rounded-button--theme-white:active {
  background-color: #ffffff;
  color: rgba(210, 40, 86, 0.3);
  box-shadow: none;
}

.rounded-button--theme-red {
  color: #d22856;
  border-color: #d22856;
}

.rounded-button--theme-red:hover {
  color: #ffffff;
  background-color: #d22856;
}

.rounded-button--theme-red:active {
  color: rgba(255, 255, 255, 0.3);
}

.rounded-button--theme-gray {
  color: #283645;
  opacity: 0.1;
  border-color: #283645;
}

.rounded-button--theme-gray:hover {
  opacity: 0.3;
}

.rounded-button--theme-gray:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #283645;
}

.slider-controls {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 140px;
}

.slider-controls__toggle {
  width: 12px;
  height: 12px;
  margin-right: 6.5px;
  margin-left: 6.5px;
  padding: 0;
  border: 3px solid #ffffff;
  border-radius: 50%;
  background-color: #283645;
  opacity: 0.3;
}

.slider-controls__toggle:hover {
  opacity: 0.6;
  cursor: pointer;
}

.slider-controls__toggle:active {
  opacity: 0.1;
}

.slider-controls__toggle--active {
  border-color: #283645;
  background-color: #ffffff;
  opacity: 1;
}

.slider-controls__toggle--active:hover,
.slider-controls__toggle--active:active {
  opacity: 1;
}

.page-header {
  margin-bottom: 40px;
  color: #ffffff;
  background-color: #283645;
}

.page-header__title-wrapper {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
}

.page-header__title {
  margin: 0;
  padding: 94px 10px 31px;
  min-height: 111px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
}

.main-nav {
  position: relative;
  margin-bottom: -66px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  background-color: rgba(0, 0, 0, 0.3);
}

.main-nav__logo {
  display: block;
  height: 22px;
  padding: 22px 20px 22px;
  color: #ffffff;
  box-shadow: none;
}

.main-nav__logo:hover[href] {
  color: #d22856;
}

.main-nav__logo:active[href] {
  color: inherit;
  opacity: 0.3;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  margin-bottom: 1px;
  box-shadow: 0 1px 0 0 #000000, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
}

.main-nav__item:hover a[href] {
  color: #d22856;
}

.main-nav__item:active a[href] {
  opacity: 0.3;
}

.main-nav__item a {
  display: block;
  padding: 15.5px 0;
  text-align: center;
  text-transform: uppercase;
}

.main-nav__toggle {
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 24px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  background-color: #ffffff;
}

.main-nav__toggle::before {
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}

.main-nav__toggle:hover::before,
.main-nav__toggle:hover::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}

.main-nav__toggle:active::before,
.main-nav__toggle:active::after {
  opacity: 0.3;
}

.downloads {
  background-image: url("../img/iphone-hand-mobile@1x.png"), url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 175px, 50% 0;
}

.downloads__inner {
  padding: 87px 0 30px 0;
}

.downloads__title {
  min-height: 60px;
  margin: 0 0 342px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
}

.downloads__link {
  display: block;
  margin-bottom: 32px;
}

.downloads__text {
  margin: 0;
  font-weight: 300;
  text-align: center;
}

.application-download {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 0 0 22px;
  padding: 0 37px;
