  padding-left: 32px;
  font-size: 12px;
  color: #d22856;
  text-transform: uppercase;
}

.images__like-link::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 1px;
  width: 13px;
  height: 12px;
  background: url("../img/icon-heart.svg") no-repeat;
}

.features__header {
  margin-bottom: 30px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
  background-color: #ffffff;
}

.features__slogan {
  margin: 0 0 27px;
}

.features__wrapper {
  position: relative;
  background-color: #f2f2f2;
}

.features__wrapper::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 31px;
  background-image: url("../img/bg-triangle-white.svg");
  background-size: 100% 31px;
}

.features__list {
  min-height: 400px;
  margin: 0 0 42px;
  padding: 81px 0 287px;
  text-align: center;
  list-style: none;
  background-image: url("../img/iphone-screen-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 109%;
}

.features__item {
  position: relative;
  margin-bottom: 31px;
}

.features__item:nth-child(3n) {
  margin-right: 0;
}

.features__item--mood::before {
