}

.features__item--changes::before {
  background-image: url("../img/iphone-screen-2-desktop@1x.png");
}

.features__item--friends::before {
  background-image: url("../img/iphone-screen-3-desktop@1x.png");
}

.features__title {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 18px;
  color: #000000;
}

.features__description {
  margin: 0;
}

.article {
  display: none;
}

.article__inner-wrap {
  position: relative;
}

.article__inner-wrap::before {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 238px;
  height: 276px;
  padding-top: 12px;
  font-weight: 700;
  font-size: 96px;
  line-height: 149.5px;
  color: #d22856;
  background-image: url("../img/icon-editor-diagram-fill.svg"), url("../img/icon-timer.svg");
  background-repeat: no-repeat;
  background-position: 19px 55px, 0 0;
  background-size: 202px 202px, 238px 276px;
}

.article__title {
  margin: 0 20px 9px 0;
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  color: #000000;
}

.article__title-lower {
  white-space: nowrap;
}

.article__text {
  margin: 0 5px 10px 0;
}

.article__more {
