  text-decoration: underline;
}

.reviews {
  position: relative;
  padding-bottom: 70px;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
}

.reviews__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.reviews__quote {
  display: flex;
  flex-direction: column;
  margin: 0;
}

.reviews__text {
  order: 1;
  margin: 0;
}

.reviews__autor-name {
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 18px;
  font-style: normal;
  color: #000000;
}

.reviews__about-autor {
  margin: 0;
  font-size: 14px;
}

.reviews__control {
  position: absolute;
  top: 142px;
  width: 22px;
  height: 41px;
  border: none;
  background-color: transparent;
  opacity: 0.3;
}

.reviews__control:hover {
  opacity: 0.6;
}

.reviews__control:active {
  opacity: 0.1;
}

.reviews__control--prev {
  left: 0;
  background-image: url("../img/icon-review-arrow-left.svg");
}

.reviews__control--next {
  right: 0;
  background-image: url("../img/icon-review-arrow-right.svg");
}

.reviews__controls {
  left: 50%;
  bottom: 24px;
  transform: translateX(-50%);
}

.price {
  position: relative;
  margin-bottom: 41px;
  line-height: 30px;
  background-color: #f2f2f2;
  overflow-x: hidden;
}

.price::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 31px;
  background-image: url("../img/bg-triangle-white.svg");
  background-size: 100% 31px;
}

.price__list-wrap {
  padding: 94px 0 99px;
}

.price__list {
  width: 300%;
  border-collapse: collapse;
  transform: translateX(-33.33%);
}

.price__tariff-item {
  width: 33.3%;
  padding: 16px 0 17px;
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  border-right: 2px solid #e5e5e5;
  border-left: 2px solid #e5e5e5;
  background-color: #283645;
  overflow: hidden;
}

.price__tariff-item-inner {
  position: relative;
}

.price__tariff-item-inner::before {
  content: "Хит";
  position: absolute;
  top: -2px;
  left: -37px;
