  padding: 2px 0 6px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  transform: rotate(-45deg);
  background-color: #d22856;
  overflow: hidden;
}

.price__tariff-price {
  font-weight: 400;
  font-size: 24px;
}

.price__kit-item {
  padding: 15px 0 14px 22px;
  text-transform: uppercase;
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
}

.price__kit-item:nth-child(n+2) {
