}

.price__kit-item--yes {
  background: #ffffff url("../img/icon-yes.svg") no-repeat right 16px top 50%;
  background-size: 31px 21px;
}

.price__kit-item--no {
  background: #ffffff url("../img/icon-no.svg") no-repeat right 17px top 50%;
  background-size: 27px 26px;
}

.price__kit-item svg {
  vertical-align: middle;
}

.price__controls {
  left: 50%;
  bottom: 47px;
  transform: translateX(-50%);
}

.contacts {
  text-align: center;
  overflow-x: hidden;
}

.contacts__list-wrap {
  margin-bottom: -35px;
}

.contacts__list-wrap::after {
  content: "";
  position: relative;
  z-index: 1;
  display: block;
  height: 35px;
  background: url("../img/bg-triangle-white-footer.svg") no-repeat;
  background-size: 100% 35px;
}

.contacts__title {
  margin-top: 0;
  margin-bottom: 29px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
}

.contacts__item {
  margin: 0 0 15px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  color: #d22856;
}

.contacts__item--email {
  margin-bottom: 20px;
}

.contacts__item--address {
  margin-bottom: 38px;
}

.contacts__item-text {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #283645;
}

.contacts__mail-link:hover {
