  transform: translateX(-50%);
}

.contacts__map-marker {
  position: absolute;
  top: 47.5%;
  left: 50%;
  transform: translateX(-50%);
}

.contacts__map-iframe {
  position: relative;
  display: block;
  width: 100%;
}

.posting__invitation {
  margin: 0 0 42px;
