}

.posting__button--reset {
  margin-bottom: 0;
}

.contest {
  line-height: 30px;
}

.contest__intro {
  margin: 0 0 33px;
  padding-top: 5px;
  text-align: center;
}

.contest__fieldset-legend {
  padding: 0;
  line-height: 18px;
  color: #d22856;
  text-align: center;
  text-transform: uppercase;
}

.contest__participant {
  margin: 0 0 26px;
  padding: 0;
  border: none;
}

.contest__participant-item {
  margin: 0 0 16px;
}

.contest__text-label {
  display: block;
  margin-bottom: 6px;
  margin-left: 22px;
  color: #d22856;
  text-transform: uppercase;
}

.contest__text-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 63px;
  padding: 14px 20px 15px;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  border: 2px solid #e5e5e5;
