}

.contest__text-field:active,
.contest__text-field:focus {
  border-color: #d22856;
}

.contest__achievements {
  margin: 0 -20px 31px;
  padding: 0;
  border: none;
  background-color: #f2f2f2;
}

.contest__achievements-legend {
  margin-bottom: 44px;
  padding: 38px 50px 0;
  background-color: #f2f2f2;
}

.contest__achievements-group {
  margin: 0 20px;
  padding: 0 0 46px;
  list-style: none;
}

.contest__achievement-option {
  margin-bottom: 52px;
  padding-left: 70px;
  font-size: 18px;
}

.contest__achievement-option:last-child {
  margin-bottom: 0;
}

.contest__achievement-label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
