.contest__contacts {
  margin: 0;
  padding: 0 0 26px;
  border: none;
}

.contest__contacts-item {
  margin: 0 0 16px;
}

.contest__contacts-item--tel::after {
  background-image: url("../img/icon-phone.svg");
}

.contest__contacts-item--email::after {
  background-image: url("../img/icon-mail.svg");
}

.contest__contacts-legend {
  padding-right: 32px;
  padding-left: 32px;
}

.contest__applications-wrap {
  margin: 0 -20px 37px;
  padding-top: 37px;
  background-color: #f2f2f2;
}

.contest__applications {
  margin-right: 20px;
  margin-left: 20px;
  padding: 0;
  border: none;
}

.contest__applications-legend {
  margin-bottom: 40px;
}

.contest__application-group {
  margin: 0;
  padding: 0 0 47px;
  list-style: none;
}

.contest__application-option {
  margin-bottom: 45px;
  padding-left: 70px;
  font-size: 18px;
}

.contest__application-option:last-child {
  margin-bottom: 0;
}

.contest__application-label {
  position: relative;
