@media (max-width: 959px) {
  /*Для экранов до 959px*/

  .main-nav--opened {
    background-color: #283645;
  }

  .main-nav--opened .main-nav__list {
    display: block;
  }

  .main-nav--opened .main-nav__logo {
    margin-bottom: 1px;
    padding: 22px 20px 25px;
    box-shadow: 0 1px 0 0 #000000;
  }

  .main-nav--opened .main-nav__toggle {
    width: 24px;
    height: 23px;
  }

  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    top: 10px;
    left: -3px;
    width: 30px;
    height: 4px;
    box-shadow: none;
  }

  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }

  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }

  .main-nav--opened .main-nav__toggle:hover::before,
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:hover::after,
  .main-nav--opened .main-nav__toggle:active::after {
    box-shadow: none;
  }

  .main-nav__list {
    display: none;
  }

  .wo-desktop-hidden {
    display: none;
  }
}

@media (max-width: 659px) {
  /*Для экранов до 659px*/

  .posting__toggle {
    opacity: 0.3;
    fill: #283645;
  }

  .posting__toggle:hover {
    opacity: 0.5;
  }

  .posting__toggle:active {
    opacity: 1;
    fill: #d22856;
  }

  .posting__toggle--active {
    opacity: 1;
    fill: #d22856;
  }

  .posting__toggle--active:hover {
    opacity: 1;
  }

  .mobile-hidden {
    display: none;
  }
}

@media (min-width: 660px) and (max-width: 959px) {
